$font-black: 'Noto Sans CJK KR Black';

.tr-y-1 {
  transform: translateY(-1px);
}
.tr-y-2 {
  transform: translateY(2px);
}
.bg-disable {
  background-color: #eeeeee !important;
}

.text-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.main-bg {
  width: 100%;
  height: auto;
  z-index: -1;
  top: 0;
  left: 0;

  img {
    width: 100%;
    height: 100%;
  }
}
.align-sub {
  vertical-align: sub !important;
}
.color-text {
  color: #333 !important;
}
.color-text2 {
  color: #666 !important;
}

.color-secondary {
  color: $secondary;
}

.badge-list {
  display: inline-block;
  height: 18px;
  background-color: $color-1;
  padding: 0 7px;
  border-radius: 8px;
  text-align: center;
  line-height: 18px;
  color: #fff;
  font-size: 10px;
}

.content {
  padding-top: 30px;
}

.edu-list {
  margin-bottom: 36px;
  .edu-item {
    > .title {
      font-size: 18px;
      font-weight: bold;
      line-height: 24px;
      letter-spacing: normal;
      color: #999999;
      margin-bottom: 14px;
      padding: 0 20px;
    }

    .rounded-box {
      position: relative;
      display: block;
      width: 100%;
      height: 48px;
      line-height: 48px;
      border-radius: 34.5px;
      background-color: #f5f7f4;
      text-align: center;
      margin-bottom: 14px;

      .badge {
        position: absolute;
        top: -6px;
        right: 0;
      }
    }
  }
  &.shadow-box-list {
    .card-item {
      border-radius: 8px;
      box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.12);
      background-color: #ffffff;
      padding-bottom: 12px;
      .title {
        padding: 0 14px;
      }
      .title-sm {
        padding: 0 52px 0 14px;
      }
      .img {
        box-shadow: none;
        border-radius: 6px 6px 0 0;
      }
      .badge-list {
        right: 14px;
        bottom: 12px;
      }
    }
  }
  &.edu-list-sm {
    .link-title {
      margin-bottom: 14px;
      i.btn-tag-more-arrow-n {
        transform: translateY(1px);
      }
    }
    .card-item {
      width: 146px;
      height: auto;
      .img {
        border-radius: 5px;
      }
      .title {
        color: #333;
      }
      .title-sm {
        .won {
          font-style: normal;
        }
      }
    }
  }
  &.edu-list-post {
    .link-title {
      i.btn-tag-more-arrow-n {
        transform: translateY(0);
      }
    }
    .card-item {
      width: 146px;
      height: auto;
      .img {
        border-radius: 5px;
      }
      .title {
        color: #333;
      }
      .title-sm {
        .won {
          font-style: normal;
        }
      }
    }
  }
  &.line-bottom {
    position: relative;
    &::after {
      content: '';
      position: absolute;
      width: calc(100vw - 40px);
      height: 2px;
      background-color: rgba(224, 224, 224, 0.5);
      left: 20px;
    }
  }
}

.card-list {
  overflow: auto;

  .scroll {
    overflow: hidden;
  }

  .card-item {
    position: relative;
    float: left;
    margin-right: 18px;
    width: 256px;
    height: auto;
    padding-bottom: 32px;

    > a {
      display: block;
    }

    .img {
      width: 100%;
      display: block;
      height: auto;
      margin-bottom: 8px;
      overflow: hidden;
      border-radius: 6px;
      box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.12);

      img {
        width: 100%;
        height: 100%;
      }
    }

    .title {
      display: block;
      padding: 0 8px;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: normal;
      color: #333;
      margin-bottom: 2px;
    }

    .title-sm {
      display: block;
      position: relative;
      font-size: 14px;
      font-weight: bold;
      line-height: 20px;
      letter-spacing: normal;
      color: #333333;
      padding-left: 8px;
      padding-right: 62px;
    }

    .badge-list {
      position: absolute;
      right: 8px;
      bottom: 0;
    }

    &:last-child {
      margin-right: 0;
    }
  }
}

.link-title {
  position: relative;
  font-size: 18px;
  line-height: 24px;
  color: #666666;
  font-weight: bold;

  i {
    vertical-align: sub;
  }

  .title {
    line-height: 34px;

    a.btn {
      position: absolute;
      right: 20px;
      top: 0;
    }
  }

  .desc {
    font-size: 14px;
    color: #333333;
    font-weight: normal;
    margin-bottom: 14px;
  }
}

.event-box {
  padding: 0 20px;
  margin-bottom: 60px;

  .link-title {
    margin-bottom: 14px;
    i.btn-tag-more-arrow-n {
      vertical-align: sub;
      transform: translateY(1px);
    }
  }

  > a {
    display: block;
  }

  .img {
    display: block;
    width: 100%;
    height: auto;
    overflow: hidden;
    border-radius: 12px;
    box-shadow: 0 9px 24px 0 rgba(0, 0, 0, 0.12);

    img {
      width: 100%;
      height: 100%;
    }
  }
}

.banner-box {
  padding: 0 20px;
  margin-bottom: 50px;

  .card-list {
    width: 100%;
    height: auto;
    border-radius: 8px;
    box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.12);
    background-color: #ffffff;

    .img {
      width: 100%;
      height: auto;
      border-radius: 16px;

      img {
        width: 100%;
        height: 100%;
      }
    }

    .card-body {
      padding: 12px 14px;

      .desc {
        position: relative;
        font-size: 12px;
        font-weight: bold;
        line-height: 1.5;
        letter-spacing: normal;
        color: #999999;
        margin-bottom: 4px;

        .badge-list {
          position: absolute;
          right: 0;
          top: 0;
        }
      }

      .title {
        font-size: 16px;
        font-weight: bold;
        line-height: 22px;
        color: #333333;
        margin-bottom: 2px;
      }

      .title-sm {
        font-size: 12px;
        font-weight: normal;
        line-height: 1.5;
        color: #333333;
        margin-bottom: 4px;
      }

      .desc-sm {
        font-size: 12px;
        font-weight: normal;
        line-height: 18px;
        color: #999999;
      }
    }
  }
}

.bg-grey {
  background-color: #EFEFEF;
  border-color: #EFEFEF;
}

.top-area {
  position: relative;
  height: 20px;
  margin-bottom: 20px;
  padding: 0 20px;
  .tab-order {
    position: absolute;
    right: 20px;
    top: 0;
    ul {
      li {
        position: relative;
        margin-right: 18px;
        float: left;
        a {
          display: inline-block;
          color: #666666;
          line-height: 20px;
        }
        &.active {
          a {
            color: $primary;
          }
        }
        &:last-child {
          margin-right: 0;
          &::after {
            content: none;
          }
        }
        &::after {
          content: '';
          position: absolute;
          width: 2px;
          height: 14px;
          background-color: #d8d8d8;
          right: -11px;
          top: 3px;
        }
      }
    }
  }
}
.tag-list {
  margin-bottom: 46px;
  li {
    float: left;
    margin-right: 12px;
    width: calc(50% - 6px);
    border-radius: 8px;
    box-shadow: 0 6px 15px 0 rgba(0, 0, 0, 0.12);
    background-color: #ffffff;
    margin-bottom: 14px;
    a {
      display: block;
    }

    .img {
      position: relative;
      width: 100%;
      height: 100%;
      border-radius: 8px 8px 0 0;

      img {
        width: 100%;
        height: 100%;
      }
      .sticker-top {
        position: absolute;
        top: -6px;
        right: 6px;
        i {
          display: inline-block;
        }
      }

      .sticker-discount {
        position: absolute;
        background-image: url("../img/common/icon-list-badge-discount-percent@2x.png");
        background-size: cover;
        background-repeat: no-repeat;
        display: block;
        width: 54px;
        height: 36px;
        bottom: 6px;
        right: -5px;
        span {
          display: block;
          font-size: 16px;
          text-align: center;
          color: #fff;
          font-weight: bold;
          line-height: 36px;
          padding-top: 3px;
          padding-left: 4px;
          .percent {
            font-weight: bold;
            font-style: normal;
            font-size: 12px;
          }
        }
      }
      .sticker-free {
        position: absolute;
        display: block;
        bottom: 6px;
        right: -4px;
        i {
          width: 54px;
          height: 36px;
        }
      }
    }
    .list-body {
      position: relative;
      padding-top: 8px;
      padding-bottom: 12px;
      min-height: 87px;
      .title {
        width: 100%;
        display: block;
        height: 40px;
        padding: 0 14px;
        font-weight: bold;
        line-height: 20px;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-box-orient: vertical;
        word-wrap:break-word;
        margin-bottom: 8px;
      }
      .score {
        padding: 0 14px;
        i {
          margin-right: 4px;
          transform: translateY(-1px);
        }
      }
      .s-sticker {
        display: block;
        position: absolute;
        bottom:12px;
        right: 14px;
      }
    }
    &:nth-child(2n) {
      margin-right: 0;
    }
  }
}
.more-btn-lg {
  a {
    display: block;
    width: 100%;
    height: 60px;
    background-color: #fdf6ef;
    line-height: 60px;
    font-weight: bold;
    font-size: 16px;
    color: $primary;
    text-align: center;
  }
}
