  }
  .event-box {
    > a {
      float: left;
      width: calc(50% - 16px);
      margin-right: 32px;
      &:last-child {
        margin-right: 0;
      }
      &.img-full {
        width: 100%;
        .img {
          max-height: initial;
        }
      }
    }
  }
  .btn-text {
    position: absolute;
    color: $primary;
    font-size: 16px;
    font-weight: bold;
    padding-right: 13px;
    right: 0;
    top: 0;
  }
