      font-size: 24px;
      margin-left: 6px;
    }
  }
  .banner-box {
    .card-list {
      overflow: hidden;
      .img {
        float: left;
        width: 50%;
        border-radius: 16px 0 0 16px;
      }
      .card-body {
        position: relative;
        width: 50%;
        float: left;
        padding: 48px 40px 10px;
        .desc {
          position: static;
          font-size: 18px;
          line-height: 24px;
          .badge-list {
            right: 16px;
            top: 16px;
          }
        }
        .title {
          font-size: 32px;
          line-height: 36px;
          margin-bottom: 24px;
        }
        .title-sm {
          font-size: 16px;
          margin-bottom: 15px;
        }
        .desc-sm {
          font-size: 16px;
        }
      }
    }
  }
  .top-area {
    padding: 0 60px;
    font-size: 24px;
    height: 36px;
    line-height: 36px;
    margin-bottom: 30px;
    margin-top: 46px;
    .tab-order {
      right: 60px;
      ul {
        li {
          a {
            font-size: 16px;
            line-height: 24px;
          }
          &::after {
            top: 14px;
          }
        }
      }
    }
  }
  .tag-list {
    margin-bottom: 0;
    li {
      width: calc(20% - 39px);
      margin-right: 48px;
      margin-bottom: 46px;
      &:nth-child(2n) {
        margin-right: 48px;
      }
      &:nth-child(5n) {
        margin-right: 0;
      }
      .img {
        .sticker-top {
          right: 0;
          i {
            margin-left: -12px;
          }
          .icon-list-badge-homelearn {
            width: 80px;
            height: 24px;
          }
          .icon-list-badge-package {
            width: 64px;
            height: 24px;
          }
        }
        .sticker-discount {
          width: 76px;
          height: 48px;
          right: -7px;
          span {
            font-size: 20px;
            line-height: 48px;
            .percent {
              font-size: 16px;
            }
          }
        }
        .sticker-free {
          right: -6px;
          i {
            width: 76px;
            height: 48px;
          }
        }
      }
      .list-body {
        .title {
          font-size: 16px;
          line-height: 22px;
          height: 44px;
        }
        .score {
          font-size: 14px;
          i {
            width: 24px;
            height: 24px;
            margin-right: 2px;
          }
        }
        .s-sticker {
          i {
            width: 62px;
            height: 24px;
          }
        }
      }
    }
  }
}
